.navbar-nav {
	flex-direction: row !important;
}

.navbar-nav .nav-link {
	padding-right: 0.5rem;
	padding-left: 0.5rem;
}

.navbar-nav .nav-item:last-child .nav-link {
	padding-right: 0;
}

.navbar-brand img {
	border-radius: 5px;
}

#hero {
	padding-top: 70px;
	height: 300px;
	position: relative;
	background-image: url("granola.jpg");
	background-repeat: no-repeat;
	background-size: cover;
}

#hero .overlay {
	background-color: rgba(0, 0, 0, 0.75);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

#hero h1 {
	font-family: 'Dancing Script', cursive;
	font-size: 60px;
}

#hero p {
	margin-top: 20px;
	font-size: 16px;
	font-weight: 300;
}

#blurb {
	padding: 50px 0;
}

.blurb-row {
	padding: 30px;
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.blurb-icon {
	padding: 15px;
	height: 70px;
	width: 70px;
	color: #bebebe;
	border: 1px solid #bebebe;
	border-radius: 40px;
	margin-right: 30px;
}

.blurb-description {
	width: auto;
	color: #5a5a5a;
	text-align: left;
	display: inline-block;
	vertical-align: top;
}

.blurb-description h6 {
	font-size: 18px;
	color: #2f2f2f;
}

#fb-feed {
	padding: 40px 0;
	background-color: rgb(235, 237, 240);
}

#footer {
	height: 80px;
	line-height: 80px;
}

/* Small devices (landscape phones, 576px and up) */
@media (min-width: 576px) {
}

/* Medium devices (tablets, 768px and up) The navbar toggle appears at this breakpoint */
@media (min-width: 768px) {
	#hero {
		padding-top: 100px;
		height: 500px;
	}
	#hero h1 {
		font-size: 90px;
	}
	#hero p {
		font-size: 20px;
	}
}

/* Large devices (desktops, 992px and up) */
@media (min-width: 992px) {
}

/* Extra large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
	#hero {
		padding-top: 120px;
		height: 600px;
	}
}
